import {Router} from "aurelia-router";
import {activationStrategy} from 'aurelia-router';

export class App {
  static inject() {return [Router];}

  constructor(router) {
    this.router = router;
    this.router.configure(config => {
      config.title = 'Aurelia';
      config.map([
        {
          route: ["", "loginApplication"], name: 'loginApplication',
          moduleId: './loginApplication', nav: true, title: 'loginApplication', settings: {roles: ['admin']}
        },
        {
          route: "mainApplication", name: 'mainApplication',
          moduleId: './mainApplication', nav: true, title: 'mainApplication'
        }
      ]);
    })
  }
    determineActivationStrategy() {
      return activationStrategy.replace;
  }
}
