import {Cookies} from 'aurelia-plugins-cookies';
import {HttpClient, json} from 'aurelia-fetch-client';
import {Router} from "aurelia-router";
import {activationStrategy} from 'aurelia-router';

let http = new HttpClient();

export class LoginApplication {
  static inject() {return [Router];}

  constructor(router) {
    this.bio = "HUI";
    this.group = "Pizda";
    this.var = "102";

    this.name = "";
    this.pass = "";
    this.API = "http://localhost:12780/9/api";
    this.router = router;

    if (Cookies.get('id') !== null && Cookies.get('login') !== null) {
      window.location.replace("#/main");
      window.location.reload(false);
    }
  }

  determineActivationStrategy() {
    return activationStrategy.replace;
  }

  process() {}

  signin() {
    var name = this.getName();
    var pass = this.getPass();

    if (name == null || pass == null) {
      return;
    }

    http.fetch(this.API + "/user/login/" + name + "/" + pass)
      .then(response => response.json())
  .then(data => {
      if (data.id === 0) {
      document.getElementById("wrong").innerHTML = "Incorrect Login/Password";
      return;
    }

    Cookies.put('id', data.id);
    Cookies.put('login', data.login);
    //this.router.navigate("#/main");
    window.location.replace("#/main");
    window.location.reload(false);
  });
  }

  signup() {
    var name = this.getName();
    var pass = this.getPass();

    if (name == null || pass == null) {
      return;
    }

    http.fetch(this.API + "/user/register/" + name + "/" + pass)
      .then(response => response.json())
  .then(data => {
      if (data.id === 0) {
      document.getElementById("wrong").innerHTML = "User already exists";
      return;
    }

    Cookies.put('id', data.id);
    Cookies.put('login', data.login);
    //this.router.nagivate("#/main");
    window.location.replace("#/main");
    window.location.reload(false);
  });
  }

  getName() {
    if (this.name.length == 0) {
      document.getElementById("name_error").innerHTML = "Please specify name";
      return null;
    }
    document.getElementById("name_error").innerHTML = "";

    return this.name;
  }

  getPass() {
    if (this.pass.length == 0) {
      document.getElementById("pass_error").innerHTML = "Please specify password";
      return null;
    }
    document.getElementById("pass_error").innerHTML = "";

    return this.pass;
  }

}
